<template>
	<div class="app-wrapper">
		<header-bar></header-bar>

		<router-view></router-view>

		<footer-bar></footer-bar>
	</div>
</template>

<script>

import {headerBar, footerBar} from "./index"

export default {
    name: 'layout',
    components: {
        headerBar,
        footerBar
    }
}
</script>

<style lang="less" scoped>
	.app-wrapper {
        width: 100%;
        height: 100%;
    }
    .main-container {
        width: 100%;
        height: e("calc(100% - 1rem)");
        position: relative;
    }
</style>


