<template>
	<header class="header-wrapper df-sb">
		<div class="search">
			<i class="iconfont icon-neirongsousuo"></i>
            <input type="text" placeholder="搜索你要的产品..." readonly>
		</div>
		<div class="my-message tc">
			<i class="iconfont icon-dialog2 db"></i>
			<span>消息</span>
		</div>
	</header>
</template>

<script type="text/javascript">

export default {

}
</script>

<style lang="less" scoped>
	.header-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		z-index: 99;
		height: .5rem;
		background-color: rgba(232, 125, 125, .1);
		.my-message {
			width: .4rem;
			i {
				font-size: .18rem;
			}
			span {
				font-size: .12rem;
			}
		}
		.search {
			width: calc(100% - .4rem);	
			.iconfont {
				position: relative;
				right: -.3rem;
			}
			input {
                width: 90%;
                height: 0.3rem;
                border: none;
                outline: none;
                background: #fff;
                text-indent: 0.3rem;
                border-radius: 0.04rem;
                font-size: .14rem;
            }
		}
	}
</style>


